export default {
	model: {
		prop: 'value',
		event: 'input'
	},
	props: {
		// 
		width: {
			type: String,
			default: ''
		},
		height: {
			type: String,
			default: ''
		},
		// 
		type: {
			type: String,
			default: 'text'
		},
		// 
		options: {
			type: Array,
			default: function _default() {
				return [];
			}
		},
		// 
		value: {
			type: [String, Number, Boolean]
		},
		// 
		func: {
			type: Function,
			default: function _default(fun, param1, param2) {
				return null;
			}
		},
		// 
		display: {
			type: String,
			default: "1"
		},
		// 
		show: {
			type: Boolean,
			default: false
		},
		// 
		title: {
			type: String,
			default: ""
		},
		// 
		icon: {
			type: String,
			default: ''
		},
		// 
		unit: {
			type: String,
			default: ''
		},
		// 
		desc: {
			type: String,
			default: ""
		},
		// 
		tip: {
			type: String,
			default: ""
		},
		// 
		min: {
			type: Number,
			default: 0
		},
		// 
		max: {
			type: Number,
			default: 0
		},
		// 
		min_length: {
			type: Number,
			default: 0
		},
		// 
		max_length: {
			type: Number,
			default: 65535
		},
		// 
		field: {
			type: String,
			default: "value"
		},
    disabled: {
			type: Boolean,
			default: false
		},
    num: {
      type: Number,
      default: 5
    }
	},
	data: function data() {
		return {
			// 
			dy: this.display,
			// 
			sw: this.show,
			// 
			load: this.loading,
			// 
			oj: this.obj,
			// 
			nm: this.num,
			// 
			txt: this.text,
			// 
			val: this.value,
			// 
			dd: this.disabled
		};
	},
	methods: {
		/// 
		/// fun: 
		/// param1: 1
		/// param2: 2
		/// param3: 3
		run: function run(fun, param1, param2, param3) {
			if (this.func) {
				return this.func(param1, param2, param3);
			}

			return null;
		},
		// 
		/// query: 
		del: function del() {
			var query = {};
			query[this.field] = this.id;
			this.run('del', query);
		},
		// 
		/// query: 
		/// obj: 
		set: function set(obj) {
			var query = {};
			query[this.field] = this.id;
			this.run('set', query, obj);
		}
	}
};
