/**
 * @description 
 * @param {Number} milliSeconds 
 * @param {Object} obj 
 * @param {String} key , 
 * @example var obj = {ok: false}; sleep(2000, obj, 'ok');
 * @example sleep(2000);
 */
function sleep(milliSeconds, obj, key) {
	var endTime = new Date().getTime() + milliSeconds;
	if (obj) {
		if (key) {
			while (new Date().getTime() < endTime) {
				if (obj[key]) {
					continue;
				}
			}
		} else {
			var func = obj;
			while (new Date().getTime() < endTime) {
				if (!func()) {
					continue;
				}
			}
		}
	} else {
		while (new Date().getTime() < endTime) {}
	}
}

/**
 * @description 
 * @param {Object} obj 
 * @param {Object} query 
 * @param {Boolean} all 
 * @return {Boolean} truefalse
 */
function as(obj, query, all) {
	if (obj) {
		var bl = true;
		var type = typeof(obj);
		if (type !== typeof(query)) {
			//  
			bl = false;
		} else if (type === 'string' || type === 'bool' || type === 'number') {
			bl = obj === query;
		} else if (obj.constructor == Array) {
			// 
			var lh = obj.length;
			if (all && lh !== query.length) {
				//   
				bl = false;
			} else {
				// 
				for (var i = 0; i < lh; i++) {
					if (!as(obj[i], query[i])) {
						bl = false;
						break;
					}
				}
			}
		} else {
			// 
			if (all && Object.getOwnPropertyNames(obj).length !== Object.getOwnPropertyNames(query).length) {
				// , 
				bl = false;
			} else {
				// 
				for (var k in query) {
					if (!as(obj[k], query[k], all)) {
						bl = false;
						break;
					}
				}
			}
		}
		return bl;
	} else {
		return false;
	}
}

/**
 * @description 
 * @param {Function()} func 
 * @param {Number} times = [value] 
 */
function speeds(func, times) {
	if (!times) {
		times = 1000000;
	}
	var t1 = (new Date()).valueOf();
	for (var i = 0; i < times; i++) {
		func();
	}
	var t2 = (new Date()).valueOf();
	var s = t2 - t1;
	console.log('' + s + ', ' + (s / times) + 'ms/');
}


/**
 * @description 
 * @param {Object} objA 
 * @param {Object} objB 
 * @param {Boolean} bl 
 * @return {Object} 
 */
function push(objA, objB, bl) {
	if (!objA || !objB) {
		return;
	}
	if (bl) {
		for (var k in objB) {
			var value = objB[k];
			if (objA[k] === undefined || objA[k] === null || value === null) {
				objA[k] = value;
			} else {
				var type = typeof(objA[k]);
				var p = typeof(value);
				if (type !== p) {
					if (type === "number") {
						var n = Number(value);
						if (n === NaN) {
							n = 0;
						}
						objA[k] = n;
					} else if (type === "boolean") {
						if (value === '0' || value === 'false' || value === 'False') {
							objA[k] = false;
						} else {
							objA[k] = Boolean(value);
						}
					} else if (type === "string") {
						objA[k] = value.toString();
					} else {
						objA[k] = value;
					}
				} else if (type === "object") {
					if (objA[k].constructor == Array && value.constructor == Array) {
						objA[k].clear();
						objA[k].addList(value);
					} else {
						push(objA[k], value, bl);
					}
				} else {
					objA[k] = value;
				}
			}
		}
	} else {
		for (var k in objA) {
			var value = objB[k];
			if (value !== undefined && objA[k] !== null && value !== null) {
				var type = typeof(objA[k]);
				var p = typeof(value);
				if (type !== p) {
					if (type === "number") {
						var n = Number(value);
						if (n === NaN) {
							n = 0;
						}
						objA[k] = n;
					} else if (type === "boolean") {
						if (value === '0' || value === 'false' || value === 'False') {
							objA[k] = false;
						} else {
							objA[k] = Boolean(value);
						}
					} else if (type === "string") {
						objA[k] = value.toString();
					} else {
						objA[k] = value;
					}
				} else if (type === "object") {
					if (objA[k].constructor == Array && value.constructor == Array) {
						objA[k].clear();
						objA[k].addList(value);
					} else {
						push(objA[k], value, bl);
					}
				} else {
					objA[k] = value;
				}
			}
		}
	}
	return objA;
}

/**
 * @description 
 * @param {Object} obj 
 * @return {Object} 
 */
function clear(obj) {
	if (obj) {
		for (var k in obj) {
			var val = obj[k];
			if (val) {
				var name = typeof val === 'undefined' ? 'undefined' : typeof(val);
				switch (name) {
					case "string":
						obj[k] = "";
						break;
					case "number":
						obj[k] = 0;
						break;
					case "array":
						obj[k].clear();
						break;
					case "object":
						clear(obj[k]);
						break;
					case "function":
						break;
					case "symbol":
						obj[k] = "";
						break;
				}
			}
		}
	}
	return obj;
}

/**
 * @description json
 * @param {Object} obj 
 * @param {Boolean} format 
 * @return {String} json
 */
function toJson(obj, format) {
	if (format) {
		return JSON.stringify(obj, null, 4);
	} else {
		return JSON.stringify(obj);
	}
}

/**
 * @description url
 * @param {Object} obj 
 * @param {String} url 
 * @return {String} url
 */
function toUrl(obj, url) {
	var queryStr = "";
	for (var key in obj) {
		var value = obj[key];
		if (typeof(value) === 'number') {
			if (value > 0) {
				queryStr += "&" + key + "=" + obj[key];
			}
		} else if (value) {
			queryStr += "&" + key + "=" + encodeURI(value);
		}
	}
	if (url) {
		if (url.endWith('?') || url.endWith('&')) {
			return url + queryStr.replace('&', '');
		} else if (url.indexOf('?') === -1) {
			return url + queryStr.replace('&', '?');
		} else {
			return url + queryStr;
		}
	} else {
		return queryStr.replace('&', '');
	}
};

/**
 * @description 
 * @param {Object} obj 
 * @param {Boolean} has 
 * @return {Object} 
 */
function copy(obj, has) {
	var newObj = {};
	if (has) {
		for (var attr in obj) {
			var o = obj[attr];
			if (o) {
				newObj[attr] = o;
			}
		}
	} else {
		for (var attr in obj) {
			newObj[attr] = obj[attr];
		}
	}
	return newObj;
};

/**
 * @description 
 * @param {Object} obj 
 * @param {String} file 
 */
function keys(obj, file) {
	var text = "";
	for (var k in obj) {
		text += k + '\r\n'
	}
	console.log(text);
}

/**
 * @description 
 * @param {Object} obj 
 * @param {Object} includeZero 0
 * @return {Object}  
 */
function delete_prop(obj, includeZero) {
	var o = Object.assign({}, obj);
	if (includeZero) {
		for (var k in o) {
			var v = o[k];
			if (!v) {
				delete o[k];
			}
		}
	} else {
		for (var k in o) {
			var v = o[k];
			if (v === '' || v === null || v === undefined) {
				delete o[k];
			}
		}
	}
	return o;
}



