(function() {
	function Timer() {
		/**
		 * @description 
		 * @example 
		 * [{
			 // 
			 name: "",
			 // 
			 async run(){}
			}]
		 */
		var list = [];

		// 
		this.core;

		/**
		 * @description 
		 */
		Timer.prototype.run = function() {
			var len = list.length;
			for (var i = 0; i < len; i++) {
				var run = list[i].run;
				if (run) {
					run();
				}
			}
		};

		/**
		 * @description 
		 */
		Timer.prototype.start = function() {
			var _this = this;
			if (!this.core) {
				this.core = setInterval(this.run, 1000);
			}
		};

		/**
		 * @description 
		 */
		Timer.prototype.end = function() {
			clearInterval(this.core);
			this.core = undefined;
		};

		/**
		 * @description 
		 */
		Timer.prototype.add = function(obj) {
			if (typeof(obj) === "object") {
				if (obj.name && obj.run && typeof(obj.run) === 'function') {
					list.push(obj);
				}
			}
		};
		/**
		 * @description 
		 */
		Timer.prototype.del = function(name) {
			if (typeof(obj) === "object") {
				list.del({
					name: name
				}, true);
			}
		};
	};
	uni.timer = new Timer();
})();


