(function() {
	/**
	 * @description 
	 * @param {Object} obj 
	 * @param {Object} query 
	 * @param {Object} value 
	 * @return {Object} 
	 */
	function add(obj, query, value) {
		if (query) {
			var oj = get(obj, query);
			if (oj) {
				uni.push(oj, value, true);
			}
			return oj;
		} else {
			uni.push(obj, value, true);
			return obj;
		}
	};
	uni.add = add;

	/**
	 * @description 
	 * @param {Object} obj 
	 * @param {Object} query 
	 * @param {Object} item 
	 * @return {Object} 
	 */
	function del(obj, query, item) {
		var o = {};
		if (query) {
			o = get(obj, query);
		} else {
			o = obj;
		}
		if (!item) {
			item = Object.assign(o);
		}
		if (Array.isArray(o)) {
			o.clear();
		} else if (Array.isArray(item)) {
			for (var i = 0; i < item.length; i++) {
				var val = item[i];
				if (typeof(val) === "object") {
					del(o, null, val);
				} else {
					delete o[val];
				}
			}
		} else if (typeof(item) === "object") {
			for (var k in item) {
				if (Array.isArray(o[k])) {
					delete o[k];
				} else if (typeof(o[k]) === "object") {
					var type = typeof(item[k]);
					if (type === "object") {
						del(o[k], null, item[k]);
					} else if (type === "string" || type === "number") {
						delete o[k][item[k]];
					} else {
						delete o[k];
					}
				} else {
					delete o[k];
				}
			}
		} else {
			delete o[item];
		}
		for (var k in o) {
			if (Object.keys(o[k]).length === 0) {
				delete o[k]
			}
		}
		return o;
	};
	uni.del = del;

	/**
	 * @description 
	 * @param {Object} obj 
	 * @param {Object} query 
	 * @param {Object} value 
	 */
	function set(obj, query, value) {
		if (query) {
			var oj = get(obj, query);
			if (oj) {
				uni.push(oj, value);
			}
			return oj;
		} else {
			uni.push(obj, value);
			return obj;
		}
	};
	uni.set = set;

	function arrToObj(arr) {
		var obj = {};
		var ret = obj;
		var len = arr.length;
		for (var i = 0; i < len; i++) {
			var k = arr[i];
			if (typeof(k) == "object") {
				if (Array.isArray(k)) {
					uni.push(obj, arrToObj(k), true);
				} else {
					uni.push(obj, k, true);
				}
			} else if (!obj[k]) {
				if (len - i > 1) {
					obj[k] = {};
				} else {
					obj[k] = true;
				}
				obj = obj[k];
			}
		}
		return ret;
	}

	/**
	 * @description 
	 * @param {Object} obj 
	 * @param {Object} query
	 * @return {Object} 
	 */
	function get(obj, query) {
		var ret;
		if (typeof(obj) === 'object' && !Array.isArray(obj)) {
			// 
			if (Array.isArray(query)) {
				var ret = obj;
				// 
				for (var i = 0; i < query.length; i++) {
					var o = query[i];
					if (Array.isArray(o)) {
						ret = get(ret, o);
					} else if (typeof(o) === 'object') {
						var oj = {};
						for (var k in o) {
							if (o[k]) {
								oj[k] = get(ret[k], o[k]);
							} else {
								oj[k] = ret[k];
							}
						}
						ret = oj;
					} else {
						ret = ret[o];
						if (typeof(ret) !== 'object') {
							break;
						}
					}
				}

			} else if (typeof(query) === 'object') {
				var ret = {};
				// 
				for (var k in query) {
					ret[k] = get(obj[k], query[k]);
				}
				ret = ret;
			} else if (query) {
				if (typeof(query) == "string" || typeof(query) == "number") {
					ret = {};
					ret[query] = obj[query];
				} else {
					ret = obj;
				}
			} else {
				// query
				ret = null;
			}
		} else {
			// 
			ret = obj;
		}
		return ret;
	};
	uni.get = get;

	/**
	 * 
	 * @param {Object} obj
	 * @param {String} key  .
	 * @param {Object} value 
	 */
	function obj_for(obj, key, value) {
		if (!key) {
			return undefined;
		}
		var keys = key.split('.');
		var len = keys.length;
		if (len == 0) {
			return undefined;
		}
		var k = keys[0];
		var o = obj[k];
		if (len == 1 && value !== undefined) {
			obj[k] = value;
			o = value;
		} else if (typeof(o) == 'object') {
			if (len > 1) {
				return obj_for(o, keys.splice(1, len).join('.'), value);
			}
		} else if (len > 1) {
			return undefined;
		}
		return o;
	}

	uni.obj = obj_for;
})();

